import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { FC } from "react";
import { parseVkAudiosScript } from "../../../parseVkAudios";
import { Code } from "../../Code";
import { Stack } from "@mui/material";

const steps = [
  "Получите список аудиозаписей",
  "Create an ad group",
  "Create an ad",
];

interface VkStepsProps {
  activeStep: number;
  onNext: () => void;
  onBack: () => void;
}

export const VkSteps: FC<VkStepsProps> = ({ activeStep, onNext, onBack }) => {
  const renderStep = () => {
    switch (activeStep) {
      case 0:
        return (
          <Stack alignItems="center">
            <Typography>
              Вставьте скрипт в консоль разработчика и нажмите Enter
            </Typography>
            <Code>{parseVkAudiosScript}</Code>
            <Button variant="contained">Copy</Button>
          </Stack>
        );
      default:
        return null;
    }
  };

  return (
    <>
      <Stepper activeStep={activeStep}>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length ? (
        <>
          <Typography sx={{ mt: 2, mb: 1 }}>
            All steps completed - you&apos;re finished
          </Typography>
          <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
            <Box sx={{ flex: "1 1 auto" }} />
          </Box>
        </>
      ) : (
        <>
          {renderStep()}
          <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
            <Button
              color="inherit"
              disabled={activeStep === 0}
              onClick={onBack}
              sx={{ mr: 1 }}
            >
              Back
            </Button>
            <Box sx={{ flex: "1 1 auto" }} />
            <Button onClick={onNext}>
              {activeStep === steps.length - 1 ? "Finish" : "Next"}
            </Button>
          </Box>
        </>
      )}
    </>
  );
};
